import React from 'react'
import s from '../../components/FindUsers/User/User.module.css'


const Paginator = (props) => {

  let pageCount = Math.ceil(props.totalPageCount / props.pageSize)

  let arrPages = []

  for (let i = 1; i <= pageCount; i++) {
    arrPages.push(i)
  }

  let portionCount  = Math.ceil(pageCount / props.pageCountInGroup)
  let [portionNumber, setPortionNumber] = React.useState(1)
  let leftPortionNumber = (portionNumber - 1) * props.pageCountInGroup + 1
  let rightPortionNumber = portionNumber * props.pageCountInGroup

  return (
    <div className={s.pageNavigate}>
    {portionNumber > 1 && 
      <button onClick={() => setPortionNumber(portionNumber - 1)}>prev</button>
    }
      {arrPages
        .filter(p => p >= leftPortionNumber && p <= rightPortionNumber)
        .map(p => {
          return <span onClick={() => props.updatePage(p)} className={props.currentPage === p && s.pageActive}>{p}</span>
        })
      }
      {portionCount > portionNumber && 
        <button onClick={() => setPortionNumber(portionNumber + 1)}>next</button>
      }
    </div>
  )
}

export default Paginator
