import { createSelector } from 'reselect'

const getUsersDate = (state) => {
  return state.usersPage.usersDate
}
export const getUsersDateSuper = createSelector(getUsersDate,(usersDate) => {
  return usersDate.filter(() => true)
})
export const getPageSize = (state) => {
  return state.usersPage.pageSize
}
export const getTotalPageCount = (state) => {
  return state.usersPage.totalPageCount
}
export const getCurrentPage = (state) => {
  return state.usersPage.currentPage
}
export const isFetching = (state) => {
  return state.usersPage.isFetching
}
export const isFollowingProgress = (state) => {
  return state.usersPage.isFollowingProgress
}
