import { headerThunkCreator } from "./auth"

const SET_INITIALIZING = 'SET-INITIALIZING'


let initialState = {
  initializing: false
}

const AppReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_INITIALIZING: {
      return { ...state, initializing: true }
    }

    default:
      return state
  }
}

export const setinitializing = () => ({ type: SET_INITIALIZING })

export const setinitializingThunkCreator = () => {
  return async (dispatch) => {
    await dispatch(headerThunkCreator())
    
    dispatch(setinitializing())
  }
}


export default AppReducer