import React from 'react'
import s from './Header.module.css'
import { NavLink } from 'react-router-dom'
import logo from '../../assets/image/user.png'

const Header = (props) => {
  
  return (
    <header className={s.header}>
      <div className={s.header_row}>
        <div className={s.header_logo}><img src="https://cdn.worldvectorlogo.com/logos/puppet.svg" alt="" /></div>
        <div className={s.login}>
          {props.isRegistred
            ? <div className={s.info_user}>
              {props.login}
              <div className={s.user_image}>
                <img src={props.userImage || logo} />
              </div>
              <button onClick={props.logoutRegistration}>Log out</button>
            </div>
            : <NavLink to='/login'>login</NavLink>
          } 
        </div>
      </div>
    </header>
  )
}

export default Header