const ADD_MESSAGE = 'ADD-MESSAGE'
const DELETE_MESSAGE = 'DELETE-MESSAGE'


let initialState = {
  messageName: [
    { id: 1, name: 'Evgeniy', image: 'https://avatarko.ru/img/kartinka/1/avatarko_anonim.jpg' },
    { id: 2, name: 'Andrey', image: 'https://bipbap.ru/wp-content/uploads/2017/10/0_8eb56_842bba74_XL-640x400.jpg' },
    { id: 3, name: 'Denis', image: 'https://klike.net/uploads/posts/2019-03/1551511801_1.jpg' },
    { id: 4, name: 'Maria', image: 'https://klike.net/uploads/posts/2019-05/1556708032_1.jpg' },
    { id: 5, name: 'Pavel', image: 'https://strana.ua/img/article/2625/70_main.jpeg' },
    { id: 6, name: 'Sergey', image: 'https://cdnimg.rg.ru/img/content/132/44/35/vliianie_kofe_d_850.jpg' },
    { id: 7, name: 'Sergey', image: 'https://cdnimg.rg.ru/img/content/132/44/35/vliianie_kofe_d_850.jpg' }
  ],
  messageDate: [
    { id: 1, message: 'Hello how are you' },
    { id: 2, message: 'Fine' },
    { id: 3, message: 'Excelent!!!' }
  ]
}

const MessageReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_MESSAGE:{
      return {...state, messageDate: [...state.messageDate,{id: state.messageDate.length + 1, message: action.newMessageText}]}
    }
    case DELETE_MESSAGE: {
      return {...state, messageDate: state.messageDate.filter(item => item !== action.userId)}
    }
    default:
      return state
  }
}

export const addMessageActionCreator = (text) => ({ type: ADD_MESSAGE, newMessageText: text })
export const deleteMessage = (userId) => ({type: DELETE_MESSAGE, userId})


export default MessageReducer