import React from 'react'
import s from './User.module.css'
import logo from '../../../assets/image/user.png'
import { NavLink } from 'react-router-dom'
import Paginator from '../../../assets/paginator/paginator'


const User = (props) => {

  return (
    <div className={s.find}>
      <Paginator  totalPageCount={props.totalPageCount} 
                  pageSize={props.pageSize} 
                  updatePage={props.updatePage} 
                  currentPage={props.currentPage}
                  pageCountInGroup = {13}
      />

      <div className={s.find_title}>Users</div>
      <div className={s.container}>
        <div className={s.find_row}>
          {
            props.state.map(el => <div className={s.find_columns}>
              <div className={s.find_item}>
                <div className={s.find_item_row}>

                  <NavLink to={'/profile/' + el.id} >
                    <div className={s.item_img}>
                      <img src={el.photos.small != null ? el.photos.small : logo} alt='' />
                    </div>
                  </NavLink>
                  <div className={s.item_body}>
                    <div className={s.item_info}>
                      <div className={s.item_name}>{el.name}</div><br />
                      <div className={s.item_status}>{"props.status"}</div>
                    </div>
                    <div className={s.item_info}>
                      <div className={s.item_country}>{"props.location.country"}</div>
                      <div className={s.item_city}>{"props.location.city"}</div>
                    </div>
                  </div>
                  {el.followed
                    ? <button disabled={props.isFollowingProgress.some(id => id === el.id)} onClick={() => {
                      props.unFollowThunk(el.id)
                    }} className={s.find_follow}>UnFollow</button>
                    : <button disabled={props.isFollowingProgress.some(id => id === el.id)} onClick={() => {
                      props.followThunk(el.id)

                    }} className={s.find_follow}>Follow</button>
                  }
                </div>
              </div>
            </div>
            )
          }
        </div>
      </div>
    </div>
  )
}

export default User