import React from 'react'
import Header from './Header'
import { connect } from 'react-redux'
import { headerThunkCreator, logoutRegistrationThunkCreator } from '../../redux/auth'

class HeaderContainer extends React.Component{
  componentDidMount(){ 
    
    this.props.headerThunkCreator()
       
  }
  render(){
    return(
      <Header {...this.props} logoutRegistration={this.props.logoutRegistrationThunkCreator}/>
    )
  }
}

let mapStateToProps = (state) => {
  return{
    isRegistred: state.auth.isRegistred,
    userImage: state.auth.userImage,
    login: state.auth.login,
  }
}

export default connect(mapStateToProps,{headerThunkCreator, logoutRegistrationThunkCreator})(HeaderContainer)