import { headerComponentAPI, profileAPI } from "../api/api"
import { stopSubmit } from 'redux-form'

const SET_AUTH_DATA = 'SET-AUTH-DATA'
const SET_USER_PHOTO = 'SET-USER-PHOTO'
const SET_CAPTCHA_IMAGE = 'SET-CAPTCHA-IMAGE'

let initialState = {
  userImage: '',
  captchaImage: '',
  id: null,
  login: null,
  email: null,
  isRegistred: false
}

const AuthReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_AUTH_DATA: {
      return {
        ...state,
        ...action.data
      }
    }
    case SET_USER_PHOTO: {
      return { ...state, userImage: action.data }
    }
    case SET_CAPTCHA_IMAGE: {
      return { ...state, captchaImage: action.data }
    }

    default:
      return state
  }
}

export const setAuthData = (id, login, email, isRegistred) => ({ type: SET_AUTH_DATA, data: { id, login, email, isRegistred } })
export const setUserPhoto = (imageURL) => ({ type: SET_USER_PHOTO, data: imageURL })
export const setCaptchaImage = (url) => ({ type: SET_CAPTCHA_IMAGE, data: url })
export const setCaptcha = (captcha) => ({ type: SET_CAPTCHA, data: captcha })

export const headerThunkCreator = () => {
  return async (dispatch) => {
    let responce = await headerComponentAPI()

    if (responce.resultCode === 0) {
      let { id, login, email } = responce.data
      dispatch(setAuthData(id, login, email, true))

      let data = await profileAPI.profileUserAPI(id)

      dispatch(setUserPhoto(data.photos.small))

    }

  }
}

export const userRegistrationThunkCreator = (body) => {
  return async (dispatch) => {
    dispatch(stopSubmit('login', { _error: 'Common error' }))
    let responce = await profileAPI.userRegistration(body)

    if (responce.data.resultCode === 0) {
      dispatch(headerThunkCreator())
    } else {
      let message = responce.data.messages.length > 0 ? responce.data.messages[0] : ''
      dispatch(stopSubmit('login', { _error: message }))
    }
  }
}

export const logoutRegistrationThunkCreator = () => {
  return async (dispatch) => {
    let responce = await profileAPI.logoutRegistration()

    if (responce.data.resultCode === 0) {
      dispatch(setAuthData(null, null, null, false))
    }
  }
}

export const getCaptchaThunkCreator = () => {
  return async (dispatch) => {
    let responce = await profileAPI.getCaptacha()
    
    dispatch(setCaptchaImage(responce.data))
  }
}

export default AuthReducer