import { profileAPI } from "../api/api"
import { headerThunkCreator, setAuthData } from "./auth"

const SET_USER_ID = 'SET-USER-LOGIN-DATA'

const initialState = {

}

const LoginReducer = (state = initialState,action) => {
  switch(action.type){
    case SET_USER_ID:{
      return{
        ...state,
        userId: action.id,

      }
    }
    default:
      return state
  }
}

export const setUserId = (id) => ({type: SET_USER_ID, id})




export default LoginReducer