import { profileAPI, userAPI } from "../api/api"

const FOLLOW = 'FOLLOW'
const UNFOLLOW = 'UNFOLLOW'
const SET_USERS = 'SET-USERS'
const SET_PAGE = 'SET-PAGE'
const TOTAL_COUNT = 'TOTAL-COUNT'
const SET_PRELOADER = 'SET-PRELOADER'
const IS_FOLLOWING_USER = 'IS-FOLLOWING-USER'

let initialState = {
  usersDate: [],
  pageSize: 100,
  totalPageCount: 0,
  currentPage: 1,
  isFetching: false,
  isFollowingProgress: [],
  fakeCount: 0
}

const FindReducers = (state = initialState, action) => {
  switch (action.type) {
    case FOLLOW:
      return {
        ...state,
        usersDate: state.usersDate.map(el => {
          if (el.id === action.userId) {
            return { ...el, followed: true }
          }
          return el
        })
      }
    case UNFOLLOW:
      return {
        ...state,
        usersDate: state.usersDate.map(el => {
          if (el.id === action.userId) {
            return { ...el, followed: false }
          }
          return el
        })
      }
    case SET_USERS: {
      return { ...state, usersDate: action.users }
    }
    case SET_PAGE: {
      return { ...state, currentPage: action.pageNumber }
    }
    case TOTAL_COUNT: {
      return { ...state, totalPageCount: action.totalCount }
    }
    case SET_PRELOADER: {
      return { ...state, isFetching: action.flag }
    }
    case IS_FOLLOWING_USER: {
      return {
        ...state,
        isFollowingProgress: action.flag
          ? [...state.isFollowingProgress, action.userId]
          : state.isFollowingProgress.filter(id => id != action.userId)
      }
    }
    default:
      return state
  }
}

export const follow = (userId) => ({ type: FOLLOW, userId })
export const unfollow = (userId) => ({ type: UNFOLLOW, userId })
export const setUser = (users) => ({ type: SET_USERS, users })
export const setPage = (pageNumber) => ({ type: SET_PAGE, pageNumber })
export const setTotalCount = (totalCount) => ({ type: TOTAL_COUNT, totalCount })
export const setPreloader = (flag) => ({ type: SET_PRELOADER, flag })
export const toggleFollowingProgress = (flag, userId) => ({ type: IS_FOLLOWING_USER, flag, userId })

export const getUserThunkCreator = (currentPage, pageSize) => {
  return async (dispatch) => {
    dispatch(setPreloader(true))
    let responce = await profileAPI.getUser(currentPage, pageSize)

    dispatch(setPreloader(false))
    dispatch(setUser(responce.items))
    dispatch(setTotalCount(responce.totalCount))
  }
}

export const onPageThunkCreator = (pageNumber, pageSize) => {
  return async (dispatch) => {
    dispatch(setPage(pageNumber))
    dispatch(setPreloader(true))
    let responce = await profileAPI.getUser(pageNumber, pageSize)

    dispatch(setPreloader(false))
    dispatch(setUser(responce.items))
  }
}

export const followThunk = (userId) => {
  return async (dispatch) => {
    dispatch(toggleFollowingProgress(true, userId))
    let responce = await userAPI.followUser(userId)

    if (responce.resultCode === 0) {
      dispatch(follow(userId))
    }
    dispatch(toggleFollowingProgress(false, userId))

  }
}

export const unFollowThunk = (userId) => {
  return async (dispatch) => {
    dispatch(toggleFollowingProgress(true, userId))
    let responce = await userAPI.unFollowUser(userId)

    if (responce.resultCode === 0) {
      dispatch(unfollow(userId))
    }
    dispatch(toggleFollowingProgress(false, userId))

  }
}

export default FindReducers