import React from 'react'
import { NavLink } from 'react-router-dom'
import SideBar from '../SideBar/SideBar'
import s from './NavBar.module.css'

const  NavBar = (props) => {
  let state = props.store.getState().sidebarPage
  return (
    <nav className={s.nav}>
      <ul>
        <li><NavLink to="/profile"  className={s.sidebar_link}>Profile</NavLink></li>
        <li><NavLink to="/message"  className={s.sidebar_link}>Message</NavLink></li>
        <li><NavLink to="/friends"  className={s.sidebar_link}>Friends</NavLink></li>
        <li><NavLink to="/music"    className={s.sidebar_link}>Music</NavLink></li><br/>
        <li><NavLink to="/find"     className={s.sidebar_link}>Find Users</NavLink></li><br/>
        <li><NavLink to="/settings" className={s.sidebar_link}>Settings</NavLink></li>
      </ul>
      <SideBar item={state.sidebar}/>
    </nav>
  )
}

export default NavBar