import React, { Suspense } from 'react'
import './App.css';
import { setinitializingThunkCreator } from '../src/redux/app'
import { connect } from 'react-redux'
import NavBar from './components/NavBar/NavBar';
import { BrowserRouter, Route } from 'react-router-dom'
import Friends from './components/Friend/Friend';
import Music from './components/Music/Music';
import Settings from './components/Settings/Settings';
import FindUsersContainer from './components/FindUsers/FindUsersContainer';
import HeaderContainer from './components/Header/HeaderContainer';
import LoginPageConatiner from './components/Login/LoginPageContainer';
import Preloader from './components/Preloader/Preloader';
import SuspenceComponent from './hoc/suspence';
// import DialogsContainer from './components/Dialogs/DialogsContainer';
// import ProfileContainer from './components/Profile/ProfileContainer';



const DialogsContainer = React.lazy(() => import('./components/Dialogs/DialogsContainer'));
const ProfileContainer = React.lazy(() => import('./components/Profile/ProfileContainer'));

class App extends React.Component{
  componentDidMount(){
    this.props.setinitializingThunkCreator()
  }

  render(){
    if(!this.props.initializing) {
      return <Preloader />
    } 
    return (
      <BrowserRouter>
        <div className="app-wrapper">
          <div className="container">
            <HeaderContainer />
            <div className="flex">
              <NavBar store={this.props.store} />
              <Route path='/profile/:userId?' render={() => <Suspense fallback={<div>Загрузка...</div>}><ProfileContainer store={this.props.store}/></Suspense>} />
              <Route path='/message' component={SuspenceComponent(DialogsContainer)} />
              <Route path='/friends' component={Friends} />
              <Route path='/music' component={Music} />
              <Route path='/find' render={() => <FindUsersContainer />} />
              <Route path='/settings' component={Settings} />
              <Route path='/login' render={() => <LoginPageConatiner />} />
            </div>
          </div>
        </div>
      </BrowserRouter>
    );
  }

}

const mapStateToProps = (state) => {
  return {
    initializing: state.app.initializing
  }
}


export default connect(mapStateToProps,{setinitializingThunkCreator})(App);
