import React from 'react'
import s from './SideBar.module.css'
import SideBarItem from './SideBarItem/SideBarItem'

const  SideBar = (props) => {
  
  const  friendsArr = props.item.map(el => <SideBarItem picture={el.image} name={el.name} id={el.id}/>)

  return (
    <div className={s.sidebar}>
      <div className={s.sidebat_title}>Best Friends</div>
      <div className={s.sidebar_content}>
        <div className={s.sidebar_row}>
          {friendsArr}
        </div>
      </div>
    </div>
  )
}

export default SideBar