import React from 'react'
import { connect } from 'react-redux'
import { userRegistrationThunkCreator, getCaptchaThunkCreator } from '../../redux/auth'
import LoginPage from './LoginPage'

class LoginPageConatiner extends React.Component{
  render(){
    return(
      <div>
        <LoginPage {...this.props} userRegistration={this.props.userRegistrationThunkCreator} />
      </div>
    )
  }
}

// export default compose(
//   connect(mapStateToProps, { profileThunkCreator, getStatusThunkCreator, updateStatusThunkCreator }),
//   withRouter,
// )(ProfileContainer)


let mapStateToProps = (state) => {
  return {
    isRegistred: state.auth.isRegistred,
    captchaImage: state.auth.captchaImage
  }
}

export default connect(mapStateToProps,{userRegistrationThunkCreator, getCaptchaThunkCreator})(LoginPageConatiner)