import { profileAPI } from "../api/api"

const ADD_POST = 'ADD-POST'
const ADD_USER_PROFILE = 'ADD-USER-PROFILE'
const SET_STATUS_USER = 'SET-STATUS-USER'
const POST_DELETE = 'POST-DELETE'
const SET_USER_IMAGE = 'SET-USER-IMAGE'

let initialState = {
  postsDate: [
    { id: 1, message: "Hi, what you doing?", likesCount: 15 },
    { id: 2, message: "I love you?", likesCount: 12 },
  ],
  userProfile: null,
  status: ''
}

const ProfileReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_POST: {
      let newPost = {
        id: state.postsDate.length + 1,
        message: action.newPost,
        likesCount: 0
      }
      return { ...state, postsDate: [...state.postsDate, newPost] }
    }
    case ADD_USER_PROFILE: {
      return { ...state, userProfile: action.profile }
    }
    case SET_STATUS_USER: {
      return { ...state, status: action.status }
    }
    case POST_DELETE: {
      return { ...state, postsDate: state.postsDate.filter(post => post.id !== action.num) }
    }
    case SET_USER_IMAGE: {
      return {...state, userProfile: {...state.userProfile, photos: action.image} }
    }
    default:
      return state
  }
}

export const addPost = (text) => ({ type: ADD_POST, newPost: text })
export const addUserProfile = (profile) => ({ type: ADD_USER_PROFILE, profile })
export const setUserStatus = (status) => ({ type: SET_STATUS_USER, status })
export const postDelete = (num) => ({ type: POST_DELETE, num })
export const setPhoto = (image) => ({type: SET_USER_IMAGE, image})


export const profileThunkCreator = (userId) => {
  return async (dispatch) => {
    let responce = await profileAPI.profileUserAPI(userId)

    dispatch(addUserProfile(responce))
  }
}

export const getStatusThunkCreator = (userId) => {
  return async (dispatch) => {
    let responce = await profileAPI.getStatus(userId)

    dispatch(setUserStatus(responce.data))
  }
}

export const updateStatusThunkCreator = (status) => {
  return async (dispatch) => {
    let responce = await profileAPI.updateUserStatus(status)

    if (responce.data.resultCode === 0) {
      dispatch(setUserStatus(status))
    }
  }

}

export const setPhotoThunkCreator = (file) => {
  return async (dispatch) => {
    let responce = await profileAPI.setPhoto(file)

    if(responce.data.resultCode === 0){
      dispatch(setPhoto(responce.data.data.photos))
    }
  }
}

export default ProfileReducer