import React from 'react'
import s from './SideBarItem.module.css'

const  SideBarItem = (props) => {
  return (
    <div className={s.sidebar_columns}>
      <div className={s.sidebar_item}>
        <div className={s.sidebar_Irow}>
          <div className={s.item_img}><img src={props.picture} alt=""/></div>
          <div className={s.item_name}>{props.name}</div>
        </div>
      </div>
    </div>
  )
}

export default SideBarItem