import React from 'react'
import { Redirect } from 'react-router'
import { Field, reduxForm } from 'redux-form'
import Element from '../../assets/formControls/formControls'
import { maxLengthCreator, required } from '../utils/validator/validator'
import s from './LoginPage.module.css'

const maxLength50 = maxLengthCreator(50)
const maxLength10 = maxLengthCreator(10)

const Input = Element('input')


const LoginForm = (props) => {
  return (
    <div>
      <form className={s.form_login} onSubmit={props.handleSubmit}>
        <div className={s.form_input}>
          <Field type="email" placeholder='email' name='email' validate={[required, maxLength50]} component={Input} />
        </div>
        <div className={s.form_input}>
          <Field type="password" placeholder='password' name='password' validate={[required, maxLength50]} component={Input} />
        </div>
        <div>
          <Field component='input' type="checkbox" name='rememberMe' />Remember me
        </div>
        {props.error && <div className={s.forFormError}>{props.error}</div>}
        {props.captchaImage &&
          <div className={s.captcha}>
            <img src={props.captchaImage.url} alt="" />
            <Field type="text" name='captcha' validate={[required, maxLength10]} component={Input} />
          </div>
        }
        <button className={s.btn}>Login</button>
      </form>
    </div>
  )
}

const LoginReduxForm = reduxForm({
  form: 'login'
})(LoginForm)

const LoginPage = (props) => {
  const onSubmit = (formData) => {
    console.log(formData)
    props.userRegistration(formData)
  }

  if (props.isRegistred) return <Redirect to='/profile' />

  return (
    
    <div>
      <h1>Please Register</h1>
      <LoginReduxForm onSubmit={onSubmit} captchaImage={props.captchaImage} />
    </div>
  )
}

export default LoginPage