import { createStore, combineReducers, applyMiddleware, compose } from 'redux'
import ProfileReducer from './profile-reducer'
import MessageReducer from './message-reducer'
import SidebarReducer from './sidebar-reducer'
import FindReducers from './users-reducer'
import AuthReducers from './auth'
import thunkMiddleware from 'redux-thunk'
import { reducer as formReducer } from 'redux-form'
import LoginReducer from './login'
import AppReducer from './app'

let reducers = combineReducers({
  profilePage: ProfileReducer, //profilePage и за него отвечает ProfileReducer
  messagePage: MessageReducer, // Есть messagePage и за него отвечает MessageReducer
  sidebarPage: SidebarReducer, // Есть sidebarPage и за него отвечает SidebarReducer
  usersPage: FindReducers,
  auth: AuthReducers,
  app: AppReducer,
  login: LoginReducer,
  form: formReducer
})

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
const store = createStore(reducers, composeEnhancers(applyMiddleware(thunkMiddleware)));

window.store = store

export default store

