import React from 'react'
import { connect } from 'react-redux'
import { follow, setPage, setUser, followThunk, unFollowThunk,
  unfollow, setTotalCount, setPreloader, toggleFollowingProgress, getUserThunkCreator, onPageThunkCreator } from '../../redux/users-reducer'
import User from './User/User'
import Preloader from '../Preloader/Preloader'
import { withAuthRedirect } from '../../hoc/withAuthRedirect'
import { getCurrentPage, getPageSize, getTotalPageCount, getUsersDateSuper, isFetching, isFollowingProgress } from '../selectors/selector'

class FindUsers extends React.Component {
  componentDidMount() {
    this.props.getUsers(this.props.currentPage,this.props.pageSize)
  }
 
  updatePage = (pageNumber) => {
    this.props.onPages(pageNumber,this.props.pageSize)
  }
  render() {
    console.log('render')
    return <>
      {this.props.isFetching ? <Preloader /> : null}
      <User
        state={this.props.state}
        updatePage={this.updatePage}
        currentPage={this.props.currentPage}
        followThunk={this.props.followThunk}
        unFollowThunk={this.props.unFollowThunk}
        totalPageCount={this.props.totalPageCount}
        pageSize={this.props.pageSize}
        isFollowingProgress={this.props.isFollowingProgress}
      />
    </>
  }
}


let mapStateToProps = (state) => {
  console.log('mapStateToProps return')
  return {
    state: getUsersDateSuper(state),
    pageSize: getPageSize(state),
    totalPageCount: getTotalPageCount(state),
    currentPage: getCurrentPage(state),
    isFetching: isFetching(state),
    isFollowingProgress: isFollowingProgress(state),
  }
}

// let AuthRedirectComponent = withAuthRedirect(FindUsers)

let FindUsersContainer = connect(mapStateToProps,
  { follow, setPage, setUser, 
    setTotalCount, unfollow, setPreloader, toggleFollowingProgress, 
    followThunk, unFollowThunk,
    getUsers: getUserThunkCreator, onPages: onPageThunkCreator })(FindUsers)

export default FindUsersContainer